html,
body,
#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  user-select: none;
  background-color: white;
}

@media (max-width: 767.98px) {
  html {
    font-size: 0.7rem;
  }
}

ul {
  padding: 0;
  margin: 0;
}

.avatar {
  width: 50%;
  min-width: 150px;
  min-height: 150px;
  border-radius: 50%;
  border: 8px solid #00dbe2;
}

.name {
  color: white;
  font-weight: 300;
}

.text-notice,
.job-description {
  color: #00dbe2;
  font-weight: 300;
}

.info {
  color: #c5c4c4;
  font-weight: 300;
}

.navbar ul {
  color: #00dbe2;
}

.nav ul li:first-child:before {
  display: none;
}

