.single-page h3 {
  color: #00dbe2;
  font-weight: 300;
  margin-bottom: 15px;
}

.header {
  color: #00dbe2;
  font-weight: 300;
}

.bk-dark-blue {
  background-color: #001d33;
}

.list li {
  margin: 0 7px;
  list-style: none;
  display: inline-block;
}

.list li p {
  margin-top: 7px;
  font-size: 16px;
  text-align: center;
}

.list img {
  width: 55px;
}

.card {
  cursor: pointer;
  background-color: #001d33;
}

.card:hover .card-img-overlay {
  display: flex;
  animation: opacity 0.5s ease-in-out;
}

.card-img-overlay {
  display: none;
  background-color: #07253ce3;
}

.card-img-overlay .card-text {
  color: #ffe222;
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes height-animation {
  from {
    height: calc(30px + 1.4rem);
  }
  to {
    height: 100%;
  }
}

@keyframes height-reverse-animation {
  from {
    height: 100%;
  }
  to {
    height: calc(30px + 1.4rem);
  }
}

.form-control {
  color: #00dbe2;
  border: 2px solid #00dbe2;
  background-color: transparent;
}

.form-control:focus {
  color: #00dbe2;
  background-color: transparent;
}

.contact-icon {
  color: white;
  font-size: 50px;
}

.contact-list li:hover .contact-icon {
  color: #00dbe2;
}

.contact-list li {
  cursor: pointer;
}

.submit-button {
  color: #00dbe2;
  border-color: #00dbe2;
}

.submit-button:hover {
  color: black;
  border-color: #00dbe2;
  background-color: #00dbe2;
}

.navbar {
  bottom: 0;
  position: absolute;
  padding: 0.7rem 1rem;
}

