.navbar-dark .navbar-nav .nav-link {
  color: white;
}

.navbar-dark.bottom-menu .navbar-nav .nav-link {
  color: #00dbe2;
  font-size: 17px;
}

.navbar-dark.bottom-menu .navbar-nav .nav-link:hover,
.navbar-dark.bottom-menu .navbar-nav .nav-link:focus {
  color: white;
}

.navbar.collapsed {
  height: 100%;
  animation: height-animation 0.5s ease-out;
}

.navbar.reversed {
  animation: height-reverse-animation 0.3s ease-out;
}

.collapse.show li {
  font-size: 20px;
  animation: opacity 0.6s ease-in;
}

.navbar-dark .navbar-nav .nav-link:hover,
.navbar-dark .navbar-nav .nav-link:focus {
  color: #00dbe2;
}

.navbar-dark .navbar-nav .show > .nav-link,
.navbar-dark .navbar-nav .active > .nav-link,
.navbar-dark .navbar-nav .nav-link.show,
.navbar-dark .navbar-nav .nav-link.active {
  color: #000000;
  background-color: #00dbe2;
}

.navbar-dark .navbar-toggler {
  border-color: transparent;
}

#nav-icon {
  width: 40px;
  height: 30px;
  cursor: pointer;
  position: relative;
  transform: rotate(0deg);
  -o-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -webkit-transition: 0.5s ease-in-out;
}

#nav-icon span {
  left: 0;
  opacity: 1;
  height: 5px;
  width: 100%;
  display: block;
  position: absolute;
  border-radius: 9px;
  background: #00dbe2;
  transform: rotate(0deg);
  -o-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -webkit-transition: 0.25s ease-in-out;
}

#nav-icon span:nth-child(1) {
  top: 0px;
  transform-origin: left center;
  -o-transform-origin: left center;
  -moz-transform-origin: left center;
  -webkit-transform-origin: left center;
}

#nav-icon span:nth-child(2) {
  top: 12px;
  transform-origin: left center;
  -o-transform-origin: left center;
  -moz-transform-origin: left center;
  -webkit-transform-origin: left center;
}

#nav-icon span:nth-child(3) {
  top: 24px;
  transform-origin: left center;
  -o-transform-origin: left center;
  -moz-transform-origin: left center;
  -webkit-transform-origin: left center;
}

#nav-icon.open span:nth-child(1) {
  top: -2px;
  left: 8px;
  transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

#nav-icon.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#nav-icon.open span:nth-child(3) {
  top: 25px;
  left: 8px;
  transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

